body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.showcase{
  
background: url('https://images.pexels.com/photos/3944460/pexels-photo-3944460.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940') no-repeat right/cover;
height: 400px;
margin: 10px;
}
.showcase .overlay{
  background-color: rgba(0,0,0,0,.5);
  display: flex;
  flex-direction: column;
  
  justify-content: center;
  height: 400px;
}
.cont{
  display: grid;
  grid-column: 5;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.contone{
  width: 400px;
  border: 2px solid pink;
  margin: 10px;
  height: 500px;
  background-color: rgb(214, 205, 205);
}
.overlay h1{
  text-align: center;
}
.form-control{
  justify-content: center;
  border-radius: 20px;
}
