.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #eee;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.main-heading{
  color:rgb(243, 103, 103);
  font-size: 26px;
}
.col-md-4{
  display: inline-flex;
  color: white;
  justify-content: center;
}
.section{
  padding-top: 40px;
  padding-bottom: 40px;
}
.underline{
  height: 12px;
  width: 4rem;
  background-color: white;
  margin-top: 10px;
  margin-bottom: 10px;
}
.navbar navbar-expand-lg bg-dark{
  color:rgb(241, 111, 111);
  
}

  



